main {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    width: 90vw;
    margin: 5rem auto;
    background: #fff;
    border-radius: 0.25rem;
    padding: 2.5rem 2rem;
    max-width: 920px;
    display: grid;
    gap: 1rem 2rem;
}

.container h3 {
    line-height: 1;
    font-weight: 500;
}

.section {
    width: 90vw;
    margin: 0 auto;
    max-width: 1170px;
}

.question {
    padding: 1rem 1.5rem;
    border: 2px solid #b4345c;
    margin-bottom: 1rem;
    border-radius: 0.25rem;
    box-shadow: 0 5px 20px rgba(0,0,0,1);
}

.question h4 {
    text-transform: none;
    line-height: 1.5;
}

.question p {
    color: rgb(137, 78, 151);
    margin-bottom: 0;
    margin-top: 0.5rem;
}

.question header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.question header h4 {
    margin-bottom: 0;
}

.btn {
    background: transparent;
    border-color: transparent;
    background: #b4345c;
    cursor: pointer;
    width: 1rem;
    height: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
}